import { create } from "zustand";
import { persist } from "zustand/middleware";

type CartState = {
  cartCount: number;
  incrementCart: () => void;
  decrementCart: () => void;
  resetCart: () => void;
  dispatch: (action: { type: "increment" | "decrement" | "reset" }) => void;
};

const useCartStore = create<CartState>()(
  persist(
    (set) => ({
      cartCount: 0,

      incrementCart: () => set((state) => ({ cartCount: state.cartCount + 1 })),
      decrementCart: () =>
        set((state) => ({ cartCount: Math.max(0, state.cartCount - 1) })),
      resetCart: () => set({ cartCount: 0 }),

      dispatch: (action) =>
        set((state) => {
          switch (action.type) {
            case "increment":
              return { cartCount: state.cartCount + 1 };
            case "decrement":
              return { cartCount: Math.max(0, state.cartCount - 1) };
            case "reset":
              return { cartCount: 0 };
            default:
              return state;
          }
        }),
    }),
    {
      name: "cart-storage", // unique name for the localStorage key
      partialize: (state) => ({ cartCount: state.cartCount }), // only store cartCount in localStorage
    }
  )
);

export default useCartStore;
